import React from 'react'
import './index.less'
import NoneData from 'components/NoneData/all-none'
import connect from 'connect'
import { SearchBar, Button, WhiteSpace, WingBlank, Carousel, List, Grid, Card, TabBar } from 'antd-mobile';
import {withRouter, Redirect} from 'react-router-dom'
@connect
@withRouter
class Home extends React.Component {
    constructor(props){
        super(props);
        
        this.state = {
          navTop: false,
          value: '美食',
          data: ['1', '2', '3'],
          imgHeight: 176,
           disabled: false,
        }

        this.$tab = null;
        this.offsetTop = 0;
    }

    
    componentDidMount () {
       setTimeout(() => {
             this.setState({
               data: ['AiyWuByWklrrUDlFignR', 'TekJlZRVCjLFexlOCuWn', 'IJOtIlfsYdTyaDTRVrLI'],
             });
           }, 100);
    }


    onfocusin= () => {
      this.props.history.slideStatus = 'left'
      this.props.history.push(`/search`)
    };
    gomorenews= () => {
      this.props.history.push(`/news`)
    };
    clear = () => {
      this.setState({ value: '' });
    };
    handleClick = () => {
        this.manualFocusInst.focus();
    }

    handleScroll(){
        let sTop = document.documentElement.scrollTop || window.pageYOffset || document.body.scrollTop;

        if(!this.state.navTop && sTop >= this.offsetTop){
           this.setState({
             navTop: true
           })
        }

        if(sTop < this.offsetTop){
           this.setState({
             navTop:false
           })
        }
      }


    render () {
        const Item = List.Item;
        const Brief = Item.Brief;
        const data1 = [
          {
            icon: require('../../images/tj1.png'),
            icon1: require('../../images/tj11.png'),
          },
          {
            icon: require('../../images/tj2.png'),
            icon1: require('../../images/tj22.png'),
          },
          {
            icon: require('../../images/tj3.png'),
            icon1: require('../../images/tj33.png'),
          },
          {
            icon: require('../../images/tj4.png'),
            icon1: require('../../images/tj44.png'),
          }
        ]
        return (
            <div className="home-container">
            <SearchBar
                    placeholder="古风水墨画"
                    cancelText="搜索"
                    onClear={value => console.log(value, 'onClear')}
                    onFocus={this.onfocusin}
                    onBlur={() => console.log('onBlur')}
                    onChange={() => console.log('change')}
                  />
            <Carousel
                      autoplay={true}
                      infinite
                      beforeChange={(from, to) => console.log(`slide from ${from} to ${to}`)}
                      afterChange={index => console.log('slide to', index)}
                    >
                      {this.state.data.map(val => (
                        <a
                          key={val}
                          href="http://www.alipay.com"
                          style={{ display: 'inline-block', width: '100%', height: this.state.imgHeight }}
                        >
                          <img
                            src={require('../../images/lian.png')}
                            alt=""
                            style={{ height:'2.03rem', width: '100%', verticalAlign: 'top' }}
                            onLoad={() => {
                              // fire window resize event to change height
                              window.dispatchEvent(new Event('resize'));
                              this.setState({ imgHeight: 'auto' });
                            }}
                          />
                        </a>
                      ))}
                    </Carousel>
            <WhiteSpace />
            <List className="my-list">
                <Item onClick={e => {
                        this.props.history.push(`/news`)
                    }} extra={<div><span className="moretext">更多</span><img src={require('../../images/gd.png')} alt="LOGO" className="moreicon" /> </div>}><span className="borderju">头条</span></Item>
                {this.state.data.map(val => (
                  <Item
                            thumb={<div className="juyuan"></div>}
                            onClick={e => {
                                  this.props.history.slideStatus = 'left'
                                    this.props.history.push(`/article/1`)
                                }}
                            key={'new' + val}
                          ><span  className="jucolor">新闻标题新闻标题新闻标题新闻标题新闻标题新.....</span></Item>
                ))}
            </List>
            <WhiteSpace />
            <List className="my-list">
                <Item><span className="borderju">推荐</span></Item>
                <Grid data={data1}
                      columnNum={2}
                      hasLine={false}
                      renderItem={dataItem => (
                        <div style={{  width: '1.67rem', height: '1.25rem' }} onClick={e => {
                                  this.props.history.slideStatus = 'left'
                                    this.props.history.push(`/like`)
                                }}>
                          <img src={dataItem.icon} style={{ width: '1.67rem', height: '1.25rem' }} alt="" />
                          <div style={{ width: '0.63rem', height: '0.2rem',position: 'absolute', top:'0.26rem', left:'0', zIndex: 10 }} >
                            <img src={dataItem.icon1} style={{ width: '0.63rem', height: '0.2rem'}} alt="" />
                            <span  className="caitext">猜你喜欢</span>
                          </div>
                        </div>
                      )}
                    />
            </List>
            <WhiteSpace />
            <Card full={true} onClick={e => {
                                    this.props.history.slideStatus = 'left'
                                    this.props.history.push(`/gooddetail/1`)
                                }}>
                  <Card.Header
                    thumb={require('../../images/lian.png')}
                    thumbStyle={{width: '3.36rem',height: '1.87rem'}}
                  />
                  <Card.Body>
                    <div>名称名称名称名称名称名称名称名称名称</div>
                  </Card.Body>
                  <Card.Footer content={<div><span  className="hongtext">￥20000.00</span><span className="futext">今日价</span></div>} />
                </Card>
           <WhiteSpace size="lg" />
            <Card full={true} onClick={e => {
                                    this.props.history.slideStatus = 'left'
                                    this.props.history.push(`/gooddetail/1`)
                                }}>
                  <Card.Header
                    thumb={require('../../images/lian.png')}
                    thumbStyle={{width: '3.36rem',height: '1.87rem'}}
                  />
                  <Card.Body>
                    <div>名称名称名称名称名称名称名称名称名称</div>
                  </Card.Body>
                  <Card.Footer content={<div><span  className="hongtext">￥20000.00</span><span className="futext">今日价</span></div>} />
                </Card>
           <WhiteSpace size="lg" />
            <Card full={true} onClick={e => {
                                    this.props.history.slideStatus = 'left'
                                    this.props.history.push(`/gooddetail/1`)
                                }}>
                  <Card.Header
                    thumb={require('../../images/lian.png')}
                    thumbStyle={{width: '3.36rem',height: '1.87rem'}}
                  />
                  <Card.Body>
                    <div>名称名称名称名称名称名称名称名称名称</div>
                  </Card.Body>
                  <Card.Footer content={<div><span  className="hongtext">￥20000.00</span><span className="futext">今日价</span></div>} />
                </Card>
            <WhiteSpace size="lg" />
     </div>
        )
    }
}
export default Home



